body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-thumb {
  background: rgba(100, 100, 100, 0.1);
}
::-webkit-scrollbar-track {
  background: transparent;
}


.layout-image {
    position: absolute;
    width: 100%;
    height: 100%;
    background-repeat:no-repeat;
    background-size:cover;
    background-position: center;
    /* filter: saturate(0.4); */
    -webkit-filter: contrast(1.4) saturate(0) brightness(1);
            filter: contrast(1.4) saturate(0) brightness(1);
    /* transition: 150ms; */
    object-fit: cover;
}

.layout-image:hover {
    -webkit-filter: none;
            filter: none;
}

.layout-image.contain {
    -webkit-filter: none;
            filter: none;
    pointer-events: none;
    background-size: contain;
    object-fit: contain;
}

@media screen and (max-width: 600px) {
    .layout-image {
        /* filter: inherit; */
    }
}
.layout-content {
    width: 100%;
    height: 100%;
    position: absolute;
    overflow-y : auto;
    overflow-x : hidden;
    font-size : 14px;
    background: white;
}

.layout-content.text-node {
    margin-bottom: 30px;
}

.layout-content .layout-label {
    text-transform: uppercase;
    font-weight : bold;
    font-size : 40px;
    line-height: 0.8;
    position: -webkit-sticky;
    position: sticky;
    top : 0;
    color : #d3d3d366;
    letter-spacing: -1px;
    word-break: break-all;
    mix-blend-mode : multiply;
}

.layout-content .layout-text {
    color : dimgrey;
    /* margin-right: 40px; */
}

.layout-content .layout-text + .layout-text {
    margin-top: 14px;
}

.layout-content .layout-btn {
    position: absolute;
    right: 0;
    top: 0;
    font-size: 18px;
    height: 30px;
    width: 40px;
    text-align: center;
    line-height: 32p;
    cursor: pointer;
}

@media screen and (max-width: 600px) {
    
    .layout-content {
        font-size: 12px;
    }

    .layout-content .layout-text + .layout-text {
        margin-top: 12px;
    }

    .layout-content.text-node {
        position: unset;
        overflow: unset;
        height: unset;
    }
}

.layout-draw-lines.hidden {
    display: none;
}

.layout-draw-lines .line {
    position: absolute;
    pointer-events: none;
    background: rgba(100,100,100,0.1);
}

.layout-draw-lines .horizontal {
    width : 100%;
    height: 1px;
    margin-left: -30% !important;
    padding-left: 30%;
    margin-right: -30%;
    padding-right: 30%;
}

.layout-draw-lines .vertical {
    width: 1px;
    height: 100%;
    margin-left: -1px;
    margin-top: -30% !important;
    padding-top: 30%;
    margin-bottom: -30%;
    padding-bottom: 30%;
   
}

.layout-draw-lines .top {
    top: 0;
    left: 0;
    margin-top: -1px;
}

.layout-draw-lines .bottom {
    left: 0;
    bottom: 0;
    margin-top: 1px;
}
.layout-header {
  flex-wrap: wrap;
  overflow-x: visible;
  overflow-y: visible;
  height: "";
  position: relative;
  display: none;
}

.layout-header.selected {
  display: flex;
}

.layout-header.selected.small {
  display: unset;
}

.layout-header .layout-header-node {
  top: 0;
  z-index: 4;
  position: -webkit-sticky;
  position: sticky;
  padding-right: 10px;
  line-height: 0.8;
  text-transform: uppercase;
  letter-spacing: -1px;
  color: black;
}

.layout-header .layout-header-node.sibling {
  top: "";
  z-index: 0;
  position: relative;
  font-weight: normal;
  color: lightgrey;
}

.layout-header.small:not(.expanded) .layout-header-node.sibling {
  display: none;
}

.layout-header .layout-header-node.sibling:hover {
  color: black !important;
  -webkit-transition: 150ms;
  transition: 150ms;
}

.layout-header:not(.small) .layout-btn {
  display: none;
}

.layout.root > .layout-header .layout-header-node {
  font-weight: bold;
}
:root {
  --transitionDuration: 150ms;
  /* --gradientLines : repeating-linear-gradient(
    -45deg,
    transparent,
    transparent 1px,
    rgba(100, 100, 100, 0.1) 1px,
    rgba(100, 100, 100, 0.1) 2px
  ); */
  --gradientLines : repeating-linear-gradient(
    -45deg,
    transparent,
    transparent 1px,
   white 1px,
   white 5px
  );
}

.layout-overlay {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  background: white;
}

.layout-overlay .layout-image {
  /* filter: contrast(0.3) saturate(0) brightness(1.5); */
  -webkit-filter: contrast(100) saturate(0) brightness(100);
          filter: contrast(100) saturate(0) brightness(100);
  opacity: 0.2;
}

.layout-overlay:hover .layout-image {
  /* filter: contrast(10) saturate(0) brightness(2); */
  /* filter: ''; */
  /* filter: contrast(1.4) saturate(0) brightness(1); */
  opacity: 1;
}

.layout-overlay .layout-color {
  width: 100%;
  height: 100%;
  position: absolute;
  /* background: linear-gradient(170deg, #ffffff 0%, #ffffff7a 100%); */
  background: repeating-linear-gradient(
    -45deg,
    transparent,
    transparent 1px,
   white 1px,
   white 5px
  );
  background: var(--gradientLines);
  opacity: 1;
  -webkit-transition: 150ms;
  transition: 150ms;
  -webkit-transition: var(--transitionDuration);
  transition: var(--transitionDuration);
  will-change: opacity;
}

.layout-overlay:hover .layout-color {
  background: linear-gradient(170deg, #ffffff 0%, transparent 50%);
}

.layout-overlay .layout-label {
  position: relative;
  line-height: 0.8;
  text-transform: uppercase;
  word-break: break-all;
  /* color: #d2d2d2; */
  /* font-weight: bold; */
  left: 0;
  top: 0;
  margin-left: -2%;
  padding-left: 2%;
  letter-spacing: -1px;
}

.layout-overlay:hover .layout-label {
  color: black;
  -webkit-transition: 150ms;
  transition: 150ms;
  -webkit-transition: var(--transitionDuration);
  transition: var(--transitionDuration);
  will-change: color;
}

@media screen and (max-width: 600px) {

  /* .layout-overlay .layout-image {
    filter: none;
  } */
  
  .layout-overlay .layout-label {
    color:black;
  }

